import React, { useState, useEffect } from 'react';
import './css/FooterImage.css';

function FooterImage() {
  const [animationOverd, setAnimationOverd] = useState(0);

  const images = [
    "footer-city3-1.png",
    "footer-city3-2.png",
    "footer-city3-3.png",
    "footer-city3-4.png",
    "footer-city3-5.png",
    "footer-city3-6.png"
  ];

  const imageItems = images.map((item,index) => 
    <>
      <img key={item.toString()} src={`${process.env.PUBLIC_URL}/img/${item}`} alt="" className={animationOverd >= index + 1 ? "footer-image active" : "footer-image"} />
    </>
  );

  const footerEvent = () => {
    const promise = new Promise((resolve) => {
      setTimeout(() => {
        resolve(setAnimationOverd(1));
      },250)
    })
    promise.then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(2));
        },250)
      })
    }).then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(3));
        },250)
      })
    }).then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(4));
        },250)
      })
    }).then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(5));
        },250)
      })
    }).then((resolve) => {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(setAnimationOverd(6));
        },250)
      })
    }).then((resolve) => {
        setTimeout(() => {
          document.getElementById('footerImages').classList.add('active');
          document.getElementById('footerText').classList.add('active');
        },700)
    })
  }

  const callFooterEvent = () => {
    if(animationOverd === 0){
      footerEvent();
    }
  }

  return(
    <>
      <div className="footer-images" id="footerImages" onLoad={callFooterEvent()}>
        {imageItems}
      </div>
    </>
  )
}

export default FooterImage;