import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import HeaderNav from './HeaderNav';
import './css/Header.css';

function Header(){
  const [click, setClick] = useState(false);

  return(
    <>
      <div className="header">
        <div className="header-top" style={{backgroundImage: `url(${process.env.PUBLIC_URL}/img/header-sky2.png)`} }>
          <Link to="/" id="headerLink">
            <img src={`${process.env.PUBLIC_URL}/img/title-logo.png`} />
          </Link>
          <i className={click ? 'fas fa-times' : 'fas fa-bars'} onClick={() => setClick(!click)} />
        </div>
        <HeaderNav
          click = {click}
          setClickFalse = {() => setClick(false)}
        />
      </div>
    </>
  )
}

export default Header;